import React, { useState } from 'react';
import FilesDataSourceParams from '../data/types/FilesDataSourceParams';
import { useNavigate } from 'react-router-dom';

const FormComponent: React.FC = () => {
  const [baseUrl, setBaseUrl] = useState<string>('');
  const [locale, setLocale] = useState<string>('');
  const [filesDir, setFilesDir] = useState<string>('');
  const [authorization, setAuthorization] = useState<string>('');
  const [method, setMethod] = useState<string>('GET');
  const naviagte=useNavigate()
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    const formData: FilesDataSourceParams = {
      baseUrl,
      params: {
        'locale': locale,
        'filesDir': filesDir
      },
      headers: {
        'Authorization': authorization
      },
      method
    };
    localStorage.setItem('config',JSON.stringify(formData))
    naviagte('/image-manager')
  };

  return (
    <div className='d-flex justify-content-center align-items-center flex-column' style={{height:'90vh'}}>
  <form className=''
      style={{
        maxWidth: '400px',
        margin: 'auto',
        padding: '20px',
        border: '1px solid #ccc',
        borderRadius: '5px'
      }}
      onSubmit={handleSubmit}
    >
      <div style={{ marginBottom: '10px' }}>
        <label htmlFor="baseUrl">Base URL:</label>
        <input
          id="baseUrl"
          type="text"
          value={baseUrl}
          onChange={(e) => setBaseUrl(e.target.value)}
          style={{ width: '100%', padding: '8px', borderRadius: '5px', border: '1px solid #ccc' }}
        />
      </div>
      <div style={{ marginBottom: '10px' }}>
        <label htmlFor="locale">Locale:</label>
        <input
          id="locale"
          type="text"
          value={locale}
          onChange={(e) => setLocale(e.target.value)}
          style={{ width: '100%', padding: '8px', borderRadius: '5px', border: '1px solid #ccc' }}
        />
      </div>
      <div style={{ marginBottom: '10px' }}>
        <label htmlFor="filesDir">Files Directory:</label>
        <input
          id="filesDir"
          type="text"
          value={filesDir}
          onChange={(e) => setFilesDir(e.target.value)}
          style={{ width: '100%', padding: '8px', borderRadius: '5px', border: '1px solid #ccc' }}
        />
      </div>
      <div style={{ marginBottom: '10px' }}>
        <label htmlFor="authorization">Authorization:</label>
        <input
          id="authorization"
          type="text"
          value={authorization}
          onChange={(e) => setAuthorization(e.target.value)}
          style={{ width: '100%', padding: '8px', borderRadius: '5px', border: '1px solid #ccc' }}
        />
      </div>
      <div style={{ marginBottom: '10px' }}>
        <label htmlFor="method">Method:</label>
        <select
          id="method"
          value={method}
          onChange={(e) => setMethod(e.target.value)}
          style={{ width: '100%', padding: '8px', borderRadius: '5px', border: '1px solid #ccc' }}
        >
          <option value="GET">GET</option>
          <option value="POST">POST</option>
          {/* Add other HTTP methods as needed */}
        </select>
      </div>
      <button
        type="submit"
        style={{
          display: 'block',
          width: '100%',
          padding: '10px',
          backgroundColor: '#007bff',
          color: '#fff',
          border: 'none',
          borderRadius: '5px',
          cursor: 'pointer'
        }}
      >
        Submit
      </button>
    </form>
    </div>
  
  );
};

export default FormComponent;
