import React, { createContext, useReducer, Dispatch, useContext, ReactNode } from 'react';
import { ImageFileModel } from '../models/ImageFileModel';

export enum FileManagerActionType {
  SELECT = 'SELECT',
  CHECK_FILE = 'TOGGLE_FILE',
  UNCHECK_FILE = 'UNCHECK_FILE',
  UPDATE_FILES = 'UPDATE_FILES',
  SELECT_ALL = 'SELECT_ALL',
  UNSELECT_ALL = 'UNSELECT_ALL',
}

interface FileManagerAction {
  type: FileManagerActionType;
  payload: { files: ImageFileModel[], index: number };
}

interface FileManagerState {
  files: ImageFileModel[];
}

const initialState: FileManagerState = { files: [] };

const FileManagerContext = createContext<{ state: FileManagerState; dispatch: Dispatch<FileManagerAction> } | undefined>(undefined);
const fileManagerReducer = (state: FileManagerState, action: FileManagerAction): FileManagerState => {
  const { type, payload } = action;
  switch (type) {
    case FileManagerActionType.SELECT:
      const selectedFileModels = payload.files;
      const newFileModels = selectedFileModels.filter(selectedFileModel =>
        state.files.every(file => file.id !== selectedFileModel.id)
      );
      if (newFileModels.length > 0) {
        return {
          ...state,
          files: [...state.files, ...newFileModels],
        };
      } else {
        return {
          ...state,
          files: state.files,
        };
      }
    case FileManagerActionType.CHECK_FILE:
      state.files[payload.index].isChecked = true;

      return {
        ...state,
        files: state.files,
      };
    case FileManagerActionType.UNCHECK_FILE:
      state.files[payload.index].isChecked = false;

      return {
        ...state,
        files: state.files,
      };
    case FileManagerActionType.UPDATE_FILES:
      state.files[payload.index].file = payload.files[0].file;
      return {
        ...state,
        files: state.files,
      };

    case FileManagerActionType.SELECT_ALL:
      state.files.map((file) => file.isChecked = true)
      return {
        ...state,
        files: state.files,
      };

    case FileManagerActionType.UNSELECT_ALL:
      state.files.map((file) => file.isChecked = false)
      return {
        ...state,
        files: state.files,
      };
    default:
      return state;
  }
};

const FileManagerProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [state, dispatch] = useReducer(fileManagerReducer, initialState);

  return (
    <FileManagerContext.Provider value={{ state, dispatch }}>
      {children}
    </FileManagerContext.Provider>
  );
};

const useFileManager = () => {
  const context = useContext(FileManagerContext);
  if (!context) {
    throw new Error('useFileManager must be used within a FileManagerProvider');
  }
  return context;
};

export { FileManagerProvider, useFileManager };
