import { useState } from 'react';
import dotsIcon from '../assets/option.png';
import NiceModal from "@ebay/nice-modal-react";
import ImageCropper from "./ImageCropper";
import { useFileManager } from '../reducers/FileReducer';
import { TakeMeConstants } from '../Utils/TakeMeConstants';
import { base64ToBlob, getFileSize } from '../Utils/FileUtils';
import getImageDimensions from '../Utils/ImageDimenstions';

const Slider = () => {

  const { dispatch, state: fileManagerState } = useFileManager();
  const [activeSlide, setActiveSlide] = useState(0);
  // const [aspect, setAspect] = useState('N/A');
  const files = fileManagerState.files;
  const filteredFiles = files.filter(item => item.isChecked);

  const showAntdModal = () => {
    NiceModal.show(ImageCropper, { image: filteredFiles[activeSlide], index: files.indexOf(filteredFiles[activeSlide]), dispatch });
  };

  const handleSlideChange = async (index: number) => {
    setActiveSlide(index);
    // const aspextRatio = await getImageDimensions(new File([base64ToBlob(filteredFiles[activeSlide].file)!], filteredFiles[activeSlide].id))
    // setAspect(`${aspextRatio.aspect}`)
  };

  const openPropFile = () => {
    localStorage.setItem('data', JSON.stringify({ files}));
  }


  return (
    <div className='col-12 d-flex flex-column ' style={{ height: '45%', width: '100%' }}>
      <div className="carousel-inner" style={{ height: '100%' }}>
        {filteredFiles.map((item, index) => (
          <div
            style={{ height: '100%' }}
            key={item.id}
            className={`carousel-item ${index === activeSlide ? 'active' : ''} `}
          >
            <div className='d-flex justify-content-center align-items-center' style={{ height: '100%' }}>
              {item.file && (
                <img
                  src={item.file}
                  alt={`Slide ${index + 1}`}
                  style={{ objectFit: 'contain', height: '100%', width: '100%' }}
                />
              )}
            </div>
          </div>
        ))}
        <div className="carousel-indicators">
          {filteredFiles.map((item, index) => (
            <button
              key={item.id}
              type="button"
              onClick={() => handleSlideChange(index)}
              className={index === activeSlide ? 'active' : ''}
              style={{
                width: '12PX',
                height: '12PX',
                border: 'none',
                outline: 'none',
                marginInline: '5px',
                borderRadius: '50%',
                backgroundColor: `${index === activeSlide ? TakeMeConstants.PRIMARY_COLOR : TakeMeConstants.SECONDNARY_COLOR}`
              }}>

            </button>
          )
          )
          }
        </div>

      </div>
      <div className=" p-3 d-flex flex-row w-100 justify-content-between align-items-center ">
        <div className=" fs-4 fw-bold justify-content-start d-flex" style={{ color: getFileSize(filteredFiles[activeSlide].size) >= 1 ? TakeMeConstants.ALERT_COLOR : TakeMeConstants.PRIMARY_COLOR }}>
          {getFileSize(filteredFiles[activeSlide].size).toFixed(2) + ' MB'}
        </div>
        {/* <div className=" fs-4 fw-bold justify-content-start d-flex" style={{ color: getFileSize(filteredFiles[activeSlide].size) >= 1 ? TakeMeConstants.ALERT_COLOR : TakeMeConstants.PRIMARY_COLOR }}>
          {aspect}
        </div> */}
        <div className=" " style={{ width: '20px' }}>
          <div className="dropdown " style={{ display: filteredFiles[activeSlide].isApiImage ? 'none' : 'inline-block' }}>
            <a
              className="btn btn-tranparnt "
              href="#"
              role="button"
              data-bs-toggle="dropdown"
              aria-expanded="false"
              style={{ border: 'none' }}
            >
              <img src={dotsIcon} alt="Dots Icon" />
            </a>

            <ul className="dropdown-menu">
              <li>
                <a
                  className="dropdown-item"
                  href="#"
                  style={{ color: TakeMeConstants.CLICKABLE_TEXT_COLOR }}
                  onClick={showAntdModal}
                >
                   
                </a>
              </li>
              <li>
                <a
                  className="dropdown-item"
                  style={{ color: TakeMeConstants.CLICKABLE_TEXT_COLOR }}
                  href={`/compressor/${files.indexOf(filteredFiles[activeSlide])}`}
                  onClick={openPropFile}
                >
                  
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Slider;


