import { BrowserRouter, Route, Routes  } from 'react-router-dom';
import './App.css';
import ImageManager from './Pages/ImageManager';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import { FileManagerProvider } from './reducers/FileReducer';
import ImageCompressor from './Pages/ImageCompressor';
import FormComponent from './Pages/demo';
function App() {
  return (
    <div className="App">
      <FileManagerProvider>
      <BrowserRouter>
      
        <Routes>
        <Route  path="/image-manager" element={<FormComponent />} />
          <Route  path="/manager" element={<ImageManager />} />
          <Route path="/compressor/:id" element={<ImageCompressor  />}  />
          
        </Routes>
      </BrowserRouter>
      </FileManagerProvider>
    </div>
  );
}

export default App;
