import  {  useRef } from 'react';
import { Modal } from 'antd';
import NiceModal, { useModal } from '@ebay/nice-modal-react';
import Cropper, { ReactCropperElement } from 'react-cropper';
import "cropperjs/dist/cropper.css";
import DataUriToFile from '../Utils/DataToFile';
import { ImageFileModel } from '../models/ImageFileModel';
import { FileManagerActionType } from '../reducers/FileReducer';


type NiceModalProps={
    image:ImageFileModel,
    index:number,
    dispatch: React.Dispatch<any>
}
export default NiceModal.create(({ image,index ,dispatch  }:NiceModalProps) => {
    const cropperRef = useRef<ReactCropperElement>(null);
    const onCrop = () => {
        const cropper = cropperRef.current?.cropper;
        const croppedImageDataURL = cropper?.getCroppedCanvas().toDataURL();
        image.file=croppedImageDataURL!;
        dispatch({type: FileManagerActionType.UPDATE_FILES,payload:{ files: [image],  index }})
    };

    


    const modal = useModal();


    return (
        <Modal
            onOk={() => {
                onCrop()
                modal.hide()
            }}
            visible={modal.visible}
            onCancel={() => modal.hide()}
            afterClose={() => modal.remove()}
            width={'90vw'}
        >
            <div className='text-center' style={{ height: '100%', width: '100%' }}>
                <Cropper
                    src={image.file}
                    ref={cropperRef}
                />
            </div>
        </Modal>
    );
});


