import React, { useRef } from 'react';
import checkMark from '../assets/Ok.png';
import { ImageFileModel } from '../models/ImageFileModel';
import { useFileManager, FileManagerActionType } from '../reducers/FileReducer';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import './ImageThumbnails.css'; // Import CSS for transitions

type ImageThumbnailsProps = {
  iamgeFileModels: ImageFileModel[];
};

const ImageThumbnails: React.FC<ImageThumbnailsProps> = ({ iamgeFileModels }) => {
  const { state, dispatch } = useFileManager();

  const handleCheckboxChange = (index: number) => {
    dispatch({
      type: state.files[index].isChecked ? FileManagerActionType.UNCHECK_FILE : FileManagerActionType.CHECK_FILE,
      payload: { files: [], index },
    });
  };

  return (
    <TransitionGroup className="image-thumbnails d-flex flex-row justify-content-end gap-2" style={{flexWrap:'wrap' ,height:'20%'}}>
      {iamgeFileModels.map((imageFileModel, index) => (
        <CSSTransition key={index} timeout={500} classNames="item">
          <div className="position-relative col-2">
            {imageFileModel.isChecked && (
              <div
                className="rounded-5 position-absolute z-1 bg-white"
                style={{ width: '20px', height: '20px', top: '-5px', left: '-10px' }}
              >
                <img style={{width:'100%',height:'100%',verticalAlign:'top'}} src={checkMark} alt="Check Mark" />
              </div>
            )}
            {imageFileModel && (
              <div>
                <input
                  style={{ display: 'none' }}
                  id={`checkbox-${index}`}
                  type="checkbox"
                  onChange={() => handleCheckboxChange(index)}
                  checked={imageFileModel.isChecked}
                />
                <label htmlFor={`checkbox-${index}`} style={{width:'100%'}}>
                  <img
                    src={imageFileModel.file}
                    alt={`Thumbnail ${index + 1}`}
                    style={{ width: '100%', height: '70px' }}
                  />
                </label>
              </div>
            )}
          </div>
        </CSSTransition>
      ))}
    </TransitionGroup>
  );
};

export default ImageThumbnails;
