import axios from 'axios';
import { dataURLtoFile } from 'image-conversion';
import { ImageFileModel } from '../models/ImageFileModel';
import FilesDataSourceParams from './types/FilesDataSourceParams';

class FilesDataSource {
    static async getFilesFromDataSource(params: FilesDataSourceParams) {
        const url = constructUrl(params.baseUrl, params.params, ApiAction.GET, []);
        try {
            const response = await axios.get(url, {
                headers: params.headers
            });
            return response.data;
        } catch (error) {
            throw new Error('Failed to fetch files: ' + error);
        }
    }

    static async deleteFilesFromDataSource(params: FilesDataSourceParams, images: string[]) {
        const url = constructUrl(params.baseUrl, params.params, ApiAction.DELETE, images);
        try {
            const response = await axios.delete(url, {
                headers: params.headers
            });
            return response.data;
        } catch (error) {
            throw new Error('Failed to delete files: ' + error);
        }
    }

    static async addFilesFromDataSource(params: FilesDataSourceParams, images: ImageFileModel[]) {
        const formData = new FormData();
        for (const image of images) {
            const imageFile = await dataURLtoFile(image.file);
            formData.append('files', imageFile);
        }
        const url = constructUrl(params.baseUrl, params.params, ApiAction.ADD, []);
        try {
            const response = await axios.post(url,formData, {
                headers: {
                    ...params.headers,
                    'Content-Type': 'multipart/form-data'
                }
            });
            return response.data;
        } catch (error) {
            throw new Error('Failed to add files: ' + error);
        }
    }
}

enum ApiAction {
    GET,
    DELETE,
    ADD
}

function constructUrl(baseUrl: string, params: { [key: string]: string }, action: ApiAction, images: string[]): string {
    const urlParams = new URLSearchParams(params).toString();
    switch (action) {
        case ApiAction.GET:
            return `${baseUrl}get-files?${urlParams}`;
        case ApiAction.DELETE:
            let deleteUrl = `${baseUrl}delete-files?${urlParams}`;
            images.forEach(image => deleteUrl += `&fileNames=${image}`);
            return deleteUrl;
        case ApiAction.ADD:
            return `${baseUrl}upload-files?${urlParams}`;
        default:
            break;
    }
    return `${baseUrl}?${urlParams}`;
}

export default FilesDataSource;
