import React from 'react';
import logo from '../assets/Handwritting-logo 1.png';
import backButton from '../assets/back.png';
import { TakeMeConstants } from '../Utils/TakeMeConstants';
type TakeMeBarType={
  title:string
}
const TakeMeBar = ({ title }:TakeMeBarType) => {


  const handleBack=()=>{
    localStorage.removeItem('data')
  }

 
  return (
  <div className='col-12' style={{height: '12%'}}>
      <nav className="row align-content-center shadow-sm" style={{ backgroundColor: TakeMeConstants.PRIMARY_COLOR }}>
      <div className='col-4 d-flex align-items-center ' style={{paddingLeft:'25px'}} >
        <a href='/'>       
        <img src={backButton} className='' alt="Back Button"  style={{ maxWidth: '18px', height: '30%' }} onClick={handleBack}/></a>
      </div>
      <div className='col-4 d-flex align-items-center '>
        <div className="text-white " style={{fontSize:TakeMeConstants.LARGE_TEXT_SIZE,fontWeight:'bold'}}> {title}</div>
      </div>
      <div className='col-4 d-flex align-items-center'>
        <a className="navbar-brand" >
          <img  src={logo} style={{ maxWidth: '99px', height: '90%' }} alt="Logo" />
        </a>
      </div>
    </nav>
  </div>
  );
};

export default TakeMeBar;
