
import axios from 'axios';
import FileResponseType from '../data/types/FileResponseType';

export async function imageUrlToFile(image: FileResponseType): Promise<File> {
  try {
    const response = await axios.get(image.fileUrl, { responseType: 'arraybuffer' });
    const blob = new Blob([response.data], { type: response.headers['content-type'] });
    const url = new URL(image.fileUrl);
    const imageName = url.pathname.split('/').pop();
    return new File([blob], imageName!);
  } catch (error) {
    console.error('Error converting image URL to file:', error);
    throw error;
  }
}
