import React from 'react';

interface RoundedEdgeButtonProps {
  label: string;
  onPress: () => void;
  bgColor: string;
}

const RoundedEdgeButton: React.FC<RoundedEdgeButtonProps> = ({ label, onPress, bgColor }) => {
  return (
    <button
      className={'rounded-1 border-0 shadow-sm text-center'}
      style={{ backgroundColor: `#${bgColor}`, width: '45vw', height: '40px', borderRadius: '5px' }}
      onClick={onPress}
    >
      <a className='fs-4' style={{ textDecoration: 'none', color: `${bgColor === '07AB83' ? 'white' : '#07AB83'}` }}>
        {label}
      </a>
    </button>
  );
}

export default RoundedEdgeButton;