import React, { useEffect, useState, useMemo } from 'react';
import { base64ToBlob, getFileSize } from '../Utils/FileUtils';
import { TakeMeConstants } from '../Utils/TakeMeConstants';
import { ImageFileModel } from '../models/ImageFileModel';
import defaultImage from '../assets/Default-product-type-and-product-image 1.png';
import TakeMeBar from '../Components/TakeMeBar';
import RoundedEdgeButton from '../Components/RoundedEdgeRectangleButton';
import ReactCompareImage from 'react-compare-image';
import Monitor from '../Components/Monitor';
import { useParams } from 'react-router-dom';
import Compressor from 'compressorjs';

const ImageCompressor = () => {
  const [compressImage, setCompressImage] = useState<File|Blob>();
  const [originalSize, setOriginalSize] = useState<number>(0);
  const [compressedSize, setCompressedSize] = useState<number>(0);
  const [firstImage, setFirstImage] = useState<string>(defaultImage);
  const [secondImage, setSecondImage] = useState<string>(defaultImage);
  const [fetchedFiles, setFetchedFiles] = useState<ImageFileModel[]>([]);
  const [progressPercentage, setProgressPercentage] = useState<number>(10);
  const [isCompressing, setIsCompressing] = useState<boolean>(false); 
  const data = useMemo(() => localStorage.getItem('data'), []);
  const { id } = useParams(); 

  useEffect(() => {
    if (data) {
      const parsedData = JSON.parse(data);
      const { files } = parsedData;
      setFetchedFiles(files);
      setFirstImage(files[Number(id)].file);
      prepareCompressedImage(files[Number(id)].file, files[Number(id)].name);
    } else {
      console.log('No Data');
    }
  }, []);



  const prepareCompressedImage = async (url: string, filename: string) => {
    setIsCompressing(true); 
    setTimeout(async () => {
      try {
        const imageToCompress = base64ToBlob(url)!;
        new Compressor(imageToCompress, {
          quality: (progressPercentage*100)/100,
          success(result) {
            setCompressImage(result);
            setSecondImage(URL.createObjectURL(result));
            setOriginalSize(getFileSize(imageToCompress.size));
            setCompressedSize(getFileSize(result.size));
            setIsCompressing(false);
          },
          error(err) {
            console.log(err.message);
            setIsCompressing(false);
          },
        });
      } catch (error) {
        console.error('Error fetching data:', error);
        setIsCompressing(false);
      }
    }, 3000);
  };
  

  const handleSliderChange = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const newProgressPercentage = Number(event.target.value);
    setProgressPercentage(newProgressPercentage);
    prepareCompressedImage(fetchedFiles[Number(id)].file, fetchedFiles[Number(id)].id);
  };


  const handleSave = () => {
    if (compressImage) {
      convertFileToBase64(compressImage)
        .then(base64String => {
          console.log('Base64 representation of compressed image:', base64String);
          openPropFile(base64String);
        })
        .catch(error => {
          console.error('Error converting file to base64:', error);
        });
    }
  };

  
  const handleCancel = () => {
    openPropFile(null);
  }

  
  const convertFileToBase64 = (file: File|Blob): Promise<string> => {
    return new Promise<string>((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        const base64String = reader.result as string;
        resolve(base64String);
      };
      reader.onerror = () => {
        reject(reader.error);
      };
    });
  };

  
  const openPropFile = (base64String: string | null) => {
    if (base64String) {
      fetchedFiles[Number(id)].file = base64String;
    }
    localStorage.setItem('data', JSON.stringify({ files: fetchedFiles }));
    window.location.href = '/manager'; 
  };

  
  return (
    <div className='container ' style={{ color: TakeMeConstants.SECONDARY_TEXT_COLOR, height: '90vh' }}>
      <TakeMeBar title=''></TakeMeBar>
      <div className=' text-center' style={{ height: '15%', padding: '15px' }}>
        <div className="row justify-content-between ">
          <div className="col-3 text-center ">
            <div style={{ fontWeight: 'bold' }}></div>
            <div>{originalSize.toFixed(2)} MB</div>
          </div>
          <div className="col-3 text-center">
            <div style={{ fontWeight: 'bold' }}></div>
            <div>{compressedSize.toFixed(2)} MB</div>
          </div>
        </div>
      </div>
      {isCompressing ? ( 
      <div className='d-flex justify-content-center align-items-center' style={{ height: '45%', backgroundColor: '#F9FFF9', overflow: 'clip' }}>
        <Monitor src={firstImage}></Monitor>
        </div>
       ) : ( 
        <div className='d-flex justify-content-center align-items-center' style={{ height: '45%', backgroundColor: '#F9FFF9', overflow: 'clip' }}>
          <ReactCompareImage rightImageCss={{ objectFit: 'contain', height: '100%' }} leftImageCss={{ objectFit: 'contain', height: '100%' }} handle skeleton sliderLineWidth={5} sliderLineColor={TakeMeConstants.PRIMARY_COLOR} leftImage={firstImage} rightImage={secondImage}  />;
        </div>
      )} 
      <label className="form-label fs-2 text-center mt-3" style={{ fontWeight: 'bold', width: '100%' }}>{progressPercentage}%</label>
      <div className='text-center ' style={{ height: '5%' }}>
        <input
          type="range"
          min="1"
          max="100"
          value={progressPercentage}
          onChange={handleSliderChange}
          className=" custom-range-input  w-100"
          id="myRange"
        />
      </div>
      <div className=' d-flex flex-row justify-content-evenly align-items-center  position-fixed fixed-bottom ' style={{ height: '10%', }}>
        <RoundedEdgeButton bgColor='' label='' onPress={handleCancel} />
        <RoundedEdgeButton bgColor='07AB83' label='' onPress={handleSave} />
      </div>
    </div>
  );
};

export default ImageCompressor;
