import React from 'react';
import './Monitor.css'; // Import the CSS file for styling

interface MonitorProps {
  src: string;
}

const Monitor: React.FC<MonitorProps> = ({ src }) => {
  return (
    <div id="monitor" style={{width:'100%',height:'100%'}}>
      <div className="scan" ></div>
      <img src={src} alt="Monitor Screen" className="screen" style={{objectFit:'contain'}} />
    </div>
  );
}

export default Monitor;
