import DefaultImage from "../Components/DefaultImage";
import TakeMeBar from "../Components/TakeMeBar";
import ImageThumbnails from "../Components/ImageThumbnails";
import React, { useCallback, useEffect, useRef } from 'react';
import RoundedEdgeSquareButton from "../Components/RoundEdgeSquareButton";
import gallery from '../assets/Image.png';
import deleteAll from '../assets/Delete All.png';
import checkAll from '../assets/Check All.png';
import done from '../assets/Done.png';
import trash from '../assets/TrashCan.png';
import Slider from "../Components/Slider";
import { ImageFileModel } from "../models/ImageFileModel";
import { FileManagerActionType, useFileManager } from "../reducers/FileReducer";
import FilesDataSource from "../data/FilesDataSource";
import FilesDataSourceParams from "../data/types/FilesDataSourceParams";
import FileResponseType from "../data/types/FileResponseType";
import { imageUrlToFile } from "../Utils/ApiFilesUtils";
import 'react-toastify/dist/ReactToastify.css';
import IndexedDb from '../Utils/IndexDB'



const ImageManager = () => {

  const defaultParams: FilesDataSourceParams = JSON.parse(localStorage.getItem('config')!)


  // const defaultParams: FilesDataSourceParams = {
  //   baseUrl: "http://191.96.1.25:8080/app_test/endpoints/files/",
  //   params: {
  //     'locale': 'ar',
  //     'filesDir': '/resources/images/categories'
  //   },
  //   headers: {
  //     'Authorization': `Bearer ${CONFIG.KEY}`
  //   },
  //   method: "GET"
  // };

  const { state, dispatch } = useFileManager();
  const fileInputRef = useRef<HTMLInputElement>(null);
  const checkedFiles = state.files.filter(file => file.isChecked);

  const handleImagePicker = useCallback(() => {
    if (fileInputRef.current) {
      fileInputRef.current.click();
    }
  }, []);

  async function mapFilesToImageModels(files: File[], isApiImage: boolean, isChecked: boolean): Promise<ImageFileModel[]> {
    const promises = files.map(file => {
      return new Promise<ImageFileModel>((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
          const base64URL = reader.result as string;
          const imageModel: ImageFileModel = {
            id: file.name,
            file: base64URL,
            isChecked,
            isApiImage,
            size: file.size
          };
          console.log(imageModel.file)
          resolve(imageModel);
        };
        reader.onerror = () => {
          reject(reader.error);
        };
      });
    });

    try {
      const imageModels = await Promise.all(promises);
      return imageModels;
    } catch (error) {
      console.error('Error converting files to base64:', error);
      return [];
    }
  }


  async function handleFileInputChange(event: React.ChangeEvent<HTMLInputElement>) {
    const eventFiles = event.target.files;
    if (eventFiles) {
      const filesArray = Array.from(eventFiles);
      const mappedImageModels = await mapFilesToImageModels(filesArray, false, false);
      dispatch({ type: FileManagerActionType.SELECT, payload: { files: mappedImageModels, index: 0 } });
    }
  }


  const fetchData = async (params: FilesDataSourceParams) => {
    try {
      const response = await FilesDataSource.getFilesFromDataSource(params);
      const apiFiles = response['output'];
      const files = await Promise.all(apiFiles.map((image: FileResponseType) => imageUrlToFile(image)));
      const imageModels = await mapFilesToImageModels(files, true, true);
      dispatch({ type: FileManagerActionType.SELECT, payload: { files: imageModels, index: 0 } });
    } catch (error) {
      console.error('Error fetching or processing data:', error);
    }
  };


  const handleSave = async () => {
    const apiRemovedImages = state.files.filter(file => file.isApiImage && !file.isChecked);
    if (apiRemovedImages.length > 0) {
      try {
        const imageNames: string[] = []
        apiRemovedImages.map(file => imageNames.push(file.id))
        const response = await FilesDataSource.deleteFilesFromDataSource(defaultParams, imageNames);
        console.log(response)
      } catch (error) {
        console.log(error)

      }
    }
    const newImages = state.files.filter(file => !file.isApiImage && file.isChecked);
    if (newImages.length > 0) {
      try {
        console.log(defaultParams)
        const response = await FilesDataSource.addFilesFromDataSource(defaultParams, newImages);
        console.log(response)
      } catch (error) {
        console.log(error)

      }
    }
    localStorage.removeItem('data')
  }




  const handleDeleteAll = async () => {
    const apiRemovedImages = state.files.filter(file => file.isApiImage);
    if (apiRemovedImages.length > 0) {
      try {
        const imageNames: string[] = []
        apiRemovedImages.map(file => imageNames.push(file.id))
        const response = await FilesDataSource.deleteFilesFromDataSource(defaultParams, imageNames);
        console.log(response)
      } catch (error) {
        console.log(error)

      }
    }
    localStorage.removeItem('data')
  }




  useEffect(() => {
    console.log(defaultParams)
    if (localStorage.getItem('data')) {
      const parsedData = JSON.parse(localStorage.getItem('data')!);
      const { files } = parsedData;
      dispatch({ type: FileManagerActionType.SELECT, payload: { files: files, index: 0 } });
      localStorage.removeItem('data')
    }
    fetchData(defaultParams);


  }, []);



  return (
    <div className='container' style={{ height: '90vh' }}>
      <TakeMeBar title=" "></TakeMeBar>

      {
        checkedFiles.length > 0 ? (
          <Slider ></Slider>
        ) : (
          <DefaultImage></DefaultImage>
        )
      }
      <ImageThumbnails iamgeFileModels={state.files}></ImageThumbnails>
      <div className='d-flex flex-row justify-content-center position-fixed align-items-center gap-3 fixed-bottom bg-white z-0' style={{ height: '15%', width: '100%' }} >
        <input
          ref={fileInputRef}
          type='file'
          accept='image/*'
          style={{ display: 'none' }}
          onChange={handleFileInputChange}
          multiple
        />
        <RoundedEdgeSquareButton onButtonClick={handleDeleteAll} imageurl={trash} isAlert={true} label=""></RoundedEdgeSquareButton>
        <RoundedEdgeSquareButton onButtonClick={() => {
          dispatch({ type: FileManagerActionType.UNSELECT_ALL, payload: { files: [], index: 0 } });

        }} imageurl={deleteAll} isAlert={false} label="  "></RoundedEdgeSquareButton>
        <RoundedEdgeSquareButton onButtonClick={() => {
          dispatch({ type: FileManagerActionType.SELECT_ALL, payload: { files: [], index: 0 } });
        }} imageurl={checkAll} isAlert={false} label=" "></RoundedEdgeSquareButton>
        <RoundedEdgeSquareButton onButtonClick={handleImagePicker} imageurl={gallery} isAlert={false} label=""></RoundedEdgeSquareButton>
        <RoundedEdgeSquareButton onButtonClick={handleSave} imageurl={done} isAlert={false} label=" "></RoundedEdgeSquareButton>
      </div>

    </div>
  );
};

export default ImageManager;
